@import url(https://fonts.googleapis.com/css?family=Montserrat:200,400,600,900);
@import url(https://fonts.googleapis.com/css?family=Hind:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*, :before, :after {
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  outline: none;
  box-sizing: inherit;
  -webkit-user-drag: none;
  -webkit-text-size-adjust: 100%; }

body, html {
  font-size: 18px;
  font-family: "Hind", sans-serif;
  color: #848484;
  height: 100%;
  font-weight: 200;
  background: #f3f3f3;
  overflow: hidden;
  display: block;
  width: 100%;
  position: relative; }

h1, h2, h3, h4, h5 {
  color: black;
  font-weight: 600;
  font-family: "Montserrat", sans-serif; }
  h1 span, h2 span, h3 span, h4 span, h5 span {
    font-weight: 900;
    color: #FEC900;
    border-bottom: 2px solid #dedede;
    padding-bottom: 0px; }

.page .pagecontainer {
  min-height: 400px; }

.menu {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 16px; }
  .menu .user-img {
    height: 40px;
    width: 40px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 5px;
    margin-right: 10px; }
  .menu div {
    text-align: right; }
  .menu p {
    margin-bottom: -10px; }
  .menu span {
    list-style: none;
    color: #FEC900;
    cursor: pointer;
    display: inline-block; }

.login-form, .chatroom {
  padding: 60px;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  display: block;
  height: auto;
  background: white;
  box-shadow: 1px 0px 15px 2px rgba(0, 0, 0, 0.07);
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  border-radius: 5px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  -webkit-transition: all 0.5s cubic-bezier(0.82, 0.05, 1, 1);
  transition: all 0.5s cubic-bezier(0.82, 0.05, 1, 1); }
  .login-form.remove, .chatroom.remove {
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh); }
  .login-form form div, .chatroom form div {
    position: relative; }
    .login-form form div p, .chatroom form div p {
      position: absolute;
      opacity: 0;
      visibility: hidden;
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
      -webkit-transition: all 0.35s cubic-bezier(0.12, 1.14, 1, 1);
      transition: all 0.35s cubic-bezier(0.12, 1.14, 1, 1); }
    .login-form form div.error input, .chatroom form div.error input {
      border-color: red; }
    .login-form form div.error p, .chatroom form div.error p {
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
      opacity: 1;
      visibility: visible; }
  .login-form h2, .chatroom h2 {
    font-size: 50px;
    line-height: 1;
    font-weight: 400;
    margin-bottom: 40px; }
  .login-form p span, .chatroom p span {
    color: #e8e8e8;
    font-size: 12px;
    margin-top: -5px;
    display: block; }
  .login-form input, .chatroom input {
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px; }
    .login-form input[type="submit"], .chatroom input[type="submit"] {
      margin-left: auto;
      display: block; }

input, textarea {
  padding: 20px 30px;
  border: none;
  -webkit-transition: all 0.15s cubic-bezier(0.12, 1.14, 1, 1);
  transition: all 0.15s cubic-bezier(0.12, 1.14, 1, 1);
  border-radius: 5px;
  border: 1px solid #d8d8d8; }
  input:active, input:focus, textarea:active, textarea:focus {
    border-color: #8B6FBA; }

input[type="submit"] {
  font-family: "Montserrat", sans-serif;
  border: none;
  background: #FEC900;
  font-weight: 600;
  text-transform: uppercase;
  width: auto;
  padding: 20px 50px;
  color: black;
  font-weight: 600;
  cursor: pointer; }
  input[type="submit"]:disabled {
    opacity: .6; }

.welcome {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  text-align: center;
  -webkit-transform: translateY(-20%);
          transform: translateY(-20%);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.5s cubic-bezier(0.12, 1.14, 1, 1);
  transition: all 0.5s cubic-bezier(0.12, 1.14, 1, 1); }
  .welcome.active {
    -webkit-transition-delay: .5s;
            transition-delay: .5s;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 1;
    visibility: visible; }
  .welcome img {
    max-width: 300px; }

.chatroom {
  max-width: 500px;
  min-height: 500px;
  padding: 40px 60px;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: cubic-bezier(0.12, 1.14, 1, 1);
          animation-timing-function: cubic-bezier(0.12, 1.14, 1, 1);
  -webkit-animation-name: fadeInCenter;
          animation-name: fadeInCenter; }
  .chatroom .topbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: baseline;
            align-items: baseline;
    border-bottom: 2px solid #dedede;
    padding-bottom: 10px; }
    .chatroom .topbar img {
      max-width: 160px; }
  .chatroom .chat-container {
    position: relative;
    height: 400px;
    overflow: hidden; }
    .chatroom .chat-container .chat {
      position: absolute;
      width: 100%;
      bottom: 0; }
      .chatroom .chat-container .chat .nomsg {
        text-align: center;
        color: #dedede; }
    .chatroom .chat-container .msg {
      display: -webkit-flex;
      display: flex;
      background: white;
      margin: 10px 0; }
      .chatroom .chat-container .msg.cur {
        -webkit-flex-direction: row-reverse;
                flex-direction: row-reverse; }
        .chatroom .chat-container .msg.cur .content {
          border-top-left-radius: 5px;
          border-top-right-radius: 0px;
          margin-left: 0;
          margin-right: 5px; }
      .chatroom .chat-container .msg img {
        max-width: 30px;
        border-radius: 5px; }
      .chatroom .chat-container .msg .content {
        background: white;
        padding: 7px 20px;
        border-radius: 5px;
        border: 1px solid #dedede;
        border-top-left-radius: 0px;
        margin-left: 5px; }
  .chatroom form {
    display: -webkit-flex;
    display: flex;
    margin: 20px 0;
    height: 60px; }
    .chatroom form input[type='text'] {
      width: 70%;
      resize: none;
      margin-right: 5px; }
    .chatroom form input {
      margin-top: 0; }

@-webkit-keyframes fadeInCenter {
  0% {
    -webkit-transform: translate3d(0, -30%, 0);
            transform: translate3d(0, -30%, 0);
    opacity: 0;
    visibility: hidden; }
  100% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
    opacity: 1;
    visibility: visible; } }

@keyframes fadeInCenter {
  0% {
    -webkit-transform: translate3d(0, -30%, 0);
            transform: translate3d(0, -30%, 0);
    opacity: 0;
    visibility: hidden; }
  100% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
    opacity: 1;
    visibility: visible; } }

